@import "Colors.css";


/* ------------ IMPORTANTE --------------*/
.container{
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  flex-direction: column;
  max-width: 100%;
}

/* ----------- COLORES ---------------*/

#Dashboard{
  margin-top: 75px;
  margin-bottom: 75px;
}

#card_header{
  background-color: var(--card_header_color);
  color: var(--card_header_font_color);
}

#card_body{
  background-color:var(--card_body_color);
  color: var(--card_body_font_color);

}

/* For smartphone: */
.big-logo {
  max-width:5vh;
}

#image_button{
  margin-top: 10px;
}

/* For desktop: */
@media only screen and (min-width: 768px) {


  .big-logo {
    max-width:5vh;
  }

  #pie_card{
    width: 35rem;
  }

  #image_button{
    display: flex;
    width: 18rem;
    height: 10rem;
  }

  #web_info_card{
    width: 100vh;
    margin-bottom: 10px;
  }
}
