:root {
  --onyx: rgba(52, 58, 64, 1);
  --white: rgba(255, 255, 255, 1);
  --urobilin: rgba(242, 175, 41, 1);
  --sweet-brown: rgba(173, 52, 62, 1);
  --queen-blue: rgba(67, 124, 144, 1);

  --dark-color: var(--onyx);
  --light-color: var(--white);
  --text-color:var(--white);


  --card_header_color: rgb(27, 27, 36);
  --card_header_font_color:rgb(208, 218, 221);
  --card_body_color: rgb(36, 36, 48);
  --card_body_font_color: #ffffff;

}
